html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a:hover {
  text-decoration: none;
}

ins, a {
  text-decoration: none;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  outline: 0;
}

input:focus, select:focus, textarea:focus, button:focus {
  outline: 0;
}

[contenteditable=true] {
  padding: 0;
  margin: 0;
  text-decoration: none;
  outline: none;
}

.wrap .nav-form {
  position: fixed;
  width: 100%;
  background-color: #46C1CF;
  z-index: 2;
}

.wrap .nav-form .nav {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  height: 42px;
  width: 100%;
  max-width: 1200px;
  margin: auto 0;
  box-sizing: border-box;
}

.wrap .nav-form .nav a {
  width: 100%;
  height: 100%;
}

.wrap .nav-form .nav a .bahalogo {
  position: absolute;
  top: 0;
  left: 32px;
  height: 42px;
  width: auto;
}

.wrap .nav-form .nav .btn-valentine {
  position: absolute;
  right: 176px;
  top: 0;
  padding: 9px 0 0 22px;
  width: 128px;
  height: 42px;
  box-sizing: border-box;
  font-size: 17px;
  color: #ffffff;
  background-color: #3EADB9;
}

.wrap .nav-form .nav .btn-download {
  position: absolute;
  right: 32px;
  top: 0;
  padding: 9px 0 0 30px;
  width: 128px;
  height: 42px;
  box-sizing: border-box;
  font-size: 17px;
  color: #ffffff;
  background-color: #41B7C4;
}

.wrap .container {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.wrap .container .section-intro-header {
  position: relative;
  width: 100%;
  height: 750px;
  background-color: #ec90b0;
  overflow-x: hidden;
}

.wrap .container .section-intro-header .bg-head-fans {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-width: 320px;
  max-width: 1980px;
  height: 750px;
  overflow: hidden;
}

.wrap .container .section-intro-header .bg-head-fans #fans-head-bg {
  height: 100%;
  width: auto;
  min-height: initial;
}

.wrap .container .section-act {
  position: relative;
  padding: 42px 0;
  width: inherit;
  background: url(../img/landingpage-valentine/bg-section-ac.png) no-repeat center;
  background-size: cover;
  box-sizing: border-box;
}

.wrap .container .section-act .act-content {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(255, 255, 255, 0.7);
  max-width: 1200px;
  width: 100%;
  padding: 32px 42px 42px 32px;
  box-sizing: border-box;
  font-size: 20px;
  font-family: Microsoft JhengHei, sans-serif;
  border-radius: 5px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.1);
}

.wrap .container .section-act .act-content .act-main-title {
  margin: 42px 0;
  font-weight: 600;
  color: #323232;
  font-size: 40px;
  text-align: center;
  width: 100%;
  line-height: 67px;
}

.wrap .container .section-act .act-content .act-list {
  margin-top: 4px;
  padding: 16px;
  width: 100%;
  box-sizing: border-box;
}

.wrap .container .section-act .act-content .act-list .ic-heart {
  display: inline-block;
  width: 18px;
  height: auto;
  margin: 0px 6px 0 7px;
}

.wrap .container .section-act .act-content .act-list .act-title {
  display: inline-block;
  color: #323232;
  line-height: 40px;
  font-weight: 600;
}

.wrap .container .section-act .act-content .act-list .act-info {
  display: block;
  padding-left: 36px;
  color: #6f6f6f;
  line-height: 40px;
}

.wrap .container .section-act .act-content .act-list .img-braver {
  display: block;
  height: 180px;
  width: auto;
}

.wrap .container .section-act .act-content .act-list .act-info-detail {
  width: 100%;
  color: #6f6f6f;
  margin: 0 0 4px 36px;
}

.wrap .container .section-act .act-content .act-list .act-list-flex {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.wrap .container .section-act .act-content .act-list .act-list-flex .act-flex-content {
  margin: 0 4px 0 36px;
  flex: 1;
  min-width: 400px;
  width: 100%;
  line-height: 32px;
  color: #6f6f6f;
}

.wrap .container .section-act .act-content .act-list .act-list-flex .act-flex-content .fans-notice-text {
  font-weight: 600;
  font-size: 16px;
  color: #606060;
  margin-bottom: 16px;
}

.wrap .container .section-act .act-content .act-list .act-list-flex .act-flex-content .change-color {
  color: #308690;
  display: inline-block;
  font-weight: 600;
}

.wrap .container .section-act .act-content .act-list .act-list-flex .img-simple {
  width: auto;
  height: 280px;
  margin: 0 48px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
}

.wrap .container .section-act .act-content .act-list .list-form {
  display: block;
  list-style-type: decimal;
  margin: 0 4px 0 0;
}

.wrap .container .section-act .act-content .act-list .list-form .list-form-content {
  color: #6f6f6f;
  margin: 0 0 0 36px;
  line-height: 32px;
}

.wrap .container .section-act .btn-va-form {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  margin: 42px 0 0 0;
  width: 100%;
  max-width: 1200px;
}

.wrap .container .section-act .btn-va-form .btn-dw-google {
  position: relative;
  width: 250px;
  padding: 18px 0;
  margin-right: 42px;
  color: #ffffff;
  font-family: Microsoft JhengHei, sans-serif;
  font-size: 24px;
  text-align: center;
  background-color: #ec90b0;
  border: none;
  border-radius: 20px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.wrap .container .section-act .btn-va-form .btn-dw-google:hover {
  background-color: #b7456d;
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
}

.wrap .container .section-act .btn-va-form .btn-dw-google p {
  display: block;
  text-align: center;
  font-size: 16px;
}

.wrap .container .section-act .btn-va-form .btn-dw-apple {
  position: relative;
  width: 250px;
  padding: 18px 0;
  color: #ffffff;
  font-family: Microsoft JhengHei, sans-serif;
  font-size: 24px;
  text-align: center;
  background-color: #ec90b0;
  border: none;
  border-radius: 20px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.wrap .container .section-act .btn-va-form .btn-dw-apple:hover {
  background-color: #b7456d;
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
}

.wrap .container .section-act .btn-va-form .btn-dw-apple p {
  display: block;
  margin: 0;
  text-align: center;
  font-size: 16px;
}

.wrap .container .section-fansclub-follow {
  position: relative;
  padding: 42px 0 0 0;
  width: inherit;
