}

.wrap .container .section-fansclub-follow .follow-content .art-form .item-flex-form .fans-content .fans-hover-form .fans-name {
  position: absolute;
  bottom: 8px;
  left: 64px;
  padding: 4px 0;
  line-height: 28px;
  font-size: 20px;
  color: #ffffff;
  max-width: 208px;
  box-sizing: border-box;
}

